html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  background-color: rgba(0, 0, 0, 0.943);
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: white;
}

h1, h2, h3, h4 {
  color: white;
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

/** Media Queries **/
/* Estilos para el encabezado en estado normal */
.header {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__titulo {
  display: none;
}
@media (min-width: 768px) {
  .header__titulo {
    display: block;
    font-family: "Montserrat", sans-serif;
    font-weight: white;
    margin: 2rem 0;
    font-size: clamp(1rem, -0.8rem + 10vw, 2.5rem);
    white-space: nowrap;
    text-align: center;
  }
}
.header__titulo2 {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: white;
  margin: 2rem 0;
  font-size: clamp(1rem, -0.8rem + 10vw, 2.5rem);
  white-space: nowrap;
}
@media (min-width: 768px) {
  .header__titulo2 {
    display: none;
  }
}
.header__contenido--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__navegacion--principal {
  display: none;
}
@media (min-width: 768px) {
  .header__navegacion--principal {
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    padding: 1rem;
  }
}
@media (min-width: 768px) {
  .header__navegacion--enlace {
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(0, 255, 0);
    font-size: 2.4rem;
    padding: 1rem;
    font-size: clamp(1rem, -0.8rem + 10vw, 2.3rem);
  }
  .header__navegacion--enlace:hover {
    background-color: rgba(69, 67, 67, 0.178);
    color: rgb(102, 243, 102);
  }
}
@media (min-width: 768px) {
  .header__navegacion--enlace {
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(0, 255, 0);
    font-size: 2.4rem;
    padding: 1rem;
    font-size: clamp(1rem, -0.8rem + 10vw, 2.3rem);
  }
  .header__navegacion--enlace:hover {
    background-color: rgba(69, 67, 67, 0.178);
    color: rgb(102, 243, 102);
  }
}

.footer {
  background-color: black;
  margin-top: 5rem;
  padding-top: 3rem;
  color: white;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.footer__texto {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: rgba(0, 0, 0, 0.943);
  margin: 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
}
.footer__copyright--formulario {
  margin: 0 auto;
  padding: 1rem;
}
.footer__copyright--regular {
  font-weight: 400;
}

.imagen {
  position: relative;
  max-height: 60rem;
  height: min(95%, 60rem);
  overflow: hidden;
}
.imagen .overlay {
  position: absolute;
  background-color: rgba(69, 67, 67, 0.178);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
}
.imagen .overlay:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.imagen .overlay img {
  border-radius: 50%;
  width: 5rem;
  margin: 0 auto;
  object-fit: cover;
}

.contenido-imagen {
  width: min(95%, 60rem);
  margin: 0 auto;
  margin: 1.5rem;
  position: relative;
  text-align: center;
}
.contenido-imagen h2 {
  margin: auto;
}
.contenido-imagen p {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .contenido-imagen {
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: rgba(69, 67, 67, 0.178);
    padding: 2.5rem 1rem;
    transform: translate(-50%, -50%);
  }
}

.titulo_portafolio {
  width: min(95%, 80rem);
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  font-weight: 700;
  margin-bottom: 2rem;
  color: rgb(0, 255, 0);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  border-bottom: 0.1rem solid rgb(0, 255, 0);
  margin-top: 2rem;
}

.proyecto {
  width: min(95%, 80rem);
  margin: 0 auto;
  width: 90%;
  padding: 1rem;
}
.proyecto__portafolio--imagen {
  position: relative;
  display: inline-block;
  overflow: hidden;
}
.proyecto__portafolio--imagen picture img {
  width: 100%;
  transition: opacity 0.3s ease;
}
.proyecto__portafolio--imagen picture img:hover {
  opacity: 0.7;
}
.proyecto__portafolio--imagen picture img:focus {
  opacity: 0.7;
}
.proyecto .descripcion {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.proyecto .descripcion h3 {
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  font-weight: 700;
  text-align: center;
  padding: 2rem;
  margin: auto;
}
.proyecto .descripcion p {
  font-size: clamp(0.5rem, -0.8rem + 10vw, 1rem);
  font-weight: 900;
  padding: 1rem;
}
.proyecto .descripcion a {
  margin-top: 1rem;
  display: block;
  text-align: center;
  font-size: clamp(0.5rem, -0.8rem + 10vw, 1rem);
  font-weight: 900;
  color: rgb(0, 255, 0);
}
.proyecto .proyecto__portafolio--imagen:hover .descripcion {
  opacity: 1;
}

.contacto {
  background-color: rgba(69, 67, 67, 0.178);
}

.forma-contacto {
  width: min(95%, 140rem);
  margin: 0 auto;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto;
}
.menu-redes__enlace::before {
  color: white;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="linkedin.com"]::before {
  content: "\f08c";
}
.menu-redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}

/* ... Tus estilos anteriores ... */
/* Estilos para el ícono mobile */
.mobile-icon {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  margin: 2rem 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-icon {
    display: none;
  }
}

#cerrar {
  display: none;
}

/* Estilos para íconos dentro del ícono mobile */
.mobile-icon i {
  font-size: 2.5rem;
  margin: 1rem;
  color: white;
}

/* Cambiar estilos cuando el menú está abierto */
.nav-abierto {
  display: block;
}
@media (min-width: 768px) {
  .nav-abierto {
    display: flex;
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(0, 255, 0);
    font-size: 2.4rem;
    padding: 1rem;
    font-size: clamp(1rem, -0.8rem + 10vw, 2.3rem);
  }
  .nav-abierto:hover {
    background-color: rgba(69, 67, 67, 0.178);
    color: rgb(102, 243, 102);
  }
}

.header__navegacion--enlace {
  margin: 1rem 0;
  font-size: clamp(1.6rem, -0.8rem + 10vw, 2.4rem);
  color: rgb(0, 255, 0);
  text-decoration: none;
}